.guide {
  padding: 70px 10px;

  .guide-header {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 15px;
  }

  .guide-title {
    margin: 24px 0 16px;
    font-size: 1.25rem;
    color: #2e2e2e;
    font-weight: 600;
    line-height: 1.2;
  }

  .guide-desc {
    background-color: #1d1f21;
    color: #c5c8c6;
    margin-bottom: 16px;
    font-size: 0.875rem;
    line-height: 26px;
    overflow-x: auto;
    border-radius: 2px;
    display: block;
    padding: 8px 12px;
    margin: 0 0 8px;
    word-break: break-all;
    word-wrap: break-word;
    border: 1px solid #e5e5e5;
    overflow: auto;
  }
}
